							</div>
						</div>
						<div class="iq-card-body">
							<div class="about-info m-0 p-0">
								<div class="row">
									<div class="col-4"><b>Nombres:</b></div>
									<div class="col-8">{{ data.name }}</div>
									<div class="col-4"><b>Apellidos:</b></div>
									<div class="col-8">
										{{ `${data.first_last_name} ${data.second_last_name}` }}
									</div>
