							<template v-slot:headerTitle>
								<h4 class="card-title">Historial de Glucosa</h4>
							</template>
							<template v-slot:body>
								<ApexChart
									v-if="showChart"
									element="home-chart-06"
									:chartOption="chartData"
								/>
							</template>
						</iq-card>
