				</b-row>
			</b-col>
		</b-row>
	</b-container>
</template>

<script>
import { xray } from "../../../config/pluginInit";
import { Toast } from "../../../mixins/toast";
import api from "../../../services/api";

export default {
	async mounted() {
		xray.index();
		await this.getData();
		await this.getAppointments();
		await this.getTreatments();
	},
	data() {
		return {
			showChart: false,
			data: {},
			appointments: [],
			treatments: [],
			user: {
				profile: require("../../../assets/images/user/1.jpg"),
			},
			chartData: {
				series: [
					{
						name: "Glucosa",
						data: [],
					},
				],
				chart: {
					height: 350,
					type: "area",
					zoom: {
						enabled: false,
					},
				},
				colors: ["#3892A6"],
				dataLabels: {
					enabled: false,
				},
				stroke: {
					curve: "smooth",
				},
				xaxis: {
					type: "datetime",
					categories: [],
				},
				tooltip: {
					x: {
						format: "dd/MM/yy",
					},
				},
			},
		};
	},
	methods: {
		async getData() {
			const user = JSON.parse(localStorage.getItem("user"));
			const userID = localStorage.getItem("userEdit");
			try {
				const { data } = await api.get(`/patients/${userID ?? user.patient_id}`);
				this.data = data.data;

				const date = this.data.glucose.map((item) => item.date);
				const quantity = this.data.glucose.map((item) => item.quantity);

				this.chartData.series[0].data = quantity;
				this.chartData.xaxis.categories = date;
				this.showChart = true;

				this.user.profile = this.data.photo;
			} catch (error) {
				Toast.fire({
					icon: "error",
					title: "Error",
				});
			}
		},
		async getAppointments() {
			const user = JSON.parse(localStorage.getItem("user"));
			const userID = localStorage.getItem("userEdit");
			try {
				const { data } = await api.get(
					`/patients/${userID ?? user.patient_id}/appointments`
				);
				this.appointments = data.data;
			} catch (error) {
				Toast.fire({
					icon: "error",
					title: "Error",
				});
			}
		},
		async getTreatments() {
			const user = JSON.parse(localStorage.getItem("user"));
			const userID = localStorage.getItem("userEdit");
			try {
				const { data } = await api.get(
					`/patients/${userID ?? user.patient_id}/treatments`
				);
				this.treatments = data.data;
			} catch (error) {
				Toast.fire({
					icon: "error",
					title: "Error",
				});
			}
		},
		previewImage(event) {
			const input = event.target;

			if (input.files && input.files[0]) {
				this.uploadPhoto(input.files[0]);
				const reader = new FileReader();

				reader.onload = (e) => {
					this.user.profile = e.target.result;
				};

				reader.readAsDataURL(input.files[0]);
			}
		},
		async uploadPhoto(file) {
			const user = JSON.parse(localStorage.getItem("user"));
			const userID = localStorage.getItem("userEdit");
			try {
				const formData = new FormData();
				formData.append("photo", file);

				await api.post(`/patients/${userID ?? user.patient_id}/photo`, formData);

				Toast.fire({
					icon: "success",
					title: "Foto Actualizada",
				});
			} catch (error) {
				const errorMessage =
					error?.response?.data?.message || "Error al actualizar la configuración";
				Toast.fire({
					icon: "error",
					title: errorMessage,
				});
			}
		},
	},
};
</script>
